.header_nav {
    width: 30%;
    display: flex;
    justify-content: space-between;
    padding: 25px 100px 0 0;
}

.nav_text {
    color: rgb(255, 255, 255);
    font-size: 25px;
    font-family: 'Bayon', sans-serif;
    cursor: pointer;
    transition: all .2s;
}

.nav_text:hover {
    color: #f7d932;
    transition: all .2s;
}

@media screen and (max-width: 576px) {

    .header_nav {
        height: 100%;
        width: 50%;
        display: block;
        padding: 0;
        margin: 0 auto;
    }

    .header_nav>div {
        text-align: center;
        margin-bottom: 20px;
    }

    .header_nav>div:last-child {
        margin-bottom: 0;
    }

    .header_nav>div>a {
        font-size: 22px;
    }

    .header_nav>div:last-child>a {
        font-size: 15px;
    }

}

@media (min-width: 576px) and (max-width: 767.98px) {

    .header_nav {
        height: 100%;
        width: 30%;
        display: block;
        padding: 0;
        margin: 0 auto;
    }

    .header_nav>div {
        text-align: center;
        margin-bottom: 20px;
    }

    .header_nav>div:last-child {
        margin-bottom: 0;
    }

    .header_nav>div>a {
        font-size: 30px;
    }

    .header_nav>div:last-child>a {
        font-size: 20px;
    }

}

@media (min-width: 768px) and (max-width: 992px) {

    .header_nav {
        width: 80%;
        display: flex;
        justify-content: space-between;
        padding: 25px 50px 0 50px;
    }

}

@media (min-width: 992px) and (max-width: 1200px) {

    .header_nav {
        width: 80%;
        display: flex;
        justify-content: space-between;
        padding: 25px 100px 0 0;
    }

}

@media (min-width: 1200px) and (max-width: 1400px) {
    .header_nav {
        width: 50%;
        display: flex;
        justify-content: space-between;
        padding: 25px 100px 0 0;
    }
}

@media (min-width: 1400px) {
    .header_nav {
        width: 40%;
        display: flex;
        justify-content: space-between;
        padding: 25px 100px 0 0;
    }
}