.span {
  font-family: 'Bayon', sans-serif;
  color: white;
  text-align: center;
  width: 100%;
  font-size: 40px;
}

.span > a {
  font-family: inherit;
}

.new_post {
  width: 800px;
  padding: 25px 25px;
  background-color: #000;
  border-radius: 40px;
}

.text {
  text-align: center;
  color: white;
  margin: 0 auto;
  font-size: 30px;
  padding-bottom: 10px;
}

.post_inp {
  display: flex;
  padding: 0 25px;
}

.img {
  width: 10%;
}

.img > img {
  height: 50px;
  width: 50px;
  border-radius: 50px;
  object-fit: cover;
}

.inp {
  width: 100%;
  padding: 10px 20px;
  background-color: #000;
}

.inp_error {
  width: 100%;
  padding: 10px 20px;
  background-color: #000;
}

.inp > input {
  width: 100%;
  outline: none;
  font-size: 25px;
  padding: 2px 12.5px;
  background-color: #000;
  border: none;
  border-bottom: 1px solid #fff;
  color: #fff;
}

.inp_error > input {
  width: 100%;
  outline: none;
  font-size: 25px;
  padding: 2px 12.5px;
  background-color: #000;
  border: none;
  border-bottom: 1px solid red;
  color: #fff;
}

.inp > input:hover {
  cursor: auto;
}

.button {
  width: 100%;
  display: flex;
  justify-content: right;
}

.btn {
  font-size: 20px;
  font-weight: bold;
  padding: 10px 30px;
  background-color: #f7d932;
  border: none;
  border-radius: 50px;
  transition: all 0.2s;
}

.btn:hover {
  background-color: #c9b027;
  transition: all 0.2s;
  cursor: pointer;
}

.btn:active {
  background-color: #99861d;
}

.form {
  width: 100%;
}

@media screen and (max-width: 576px) {
  .feed {
    margin: 0 auto;
    width: 90%;
  }
  .new_post {
    width: 100%;
    padding: 15px 25px;
    background-color: #000;
    border-radius: 30px;
  }
  .span {
    width: 100%;
    font-size: 22px;
  }
  .text {
    text-align: center;
    color: white;
    margin: 0 auto;
    font-size: 18px;
    padding-bottom: 5px;
    font-weight: 300;
  }
  .post_inp {
    display: flex;
    padding: 0;
  }
  .inp > input {
    font-size: 14px;
    padding: 2px 5px;
  }
  .inp_error > input {
    font-size: 14px;
    padding: 2px 5px;
  }
  .inp {
    padding: 10px 10px;
  } 
  .inp_error {
    padding: 10px 10px;
  }
  .btn {
    font-size: 15px;
    font-weight: bold;
    padding: 10px 20px;
    background-color: #f7d932;
    border: none;
    border-radius: 50px;
    transition: all 0.2s;
  }
}
  
@media (min-width: 576px) and (max-width: 768px) {
  .feed {
    margin: 0 auto;
    width: 90%;
  }

  .new_post {
    width: 100%;
  }
}

@media (min-width: 768px) and (max-width: 992px) {

  .feed {
    width: 100%;
  }

  .new_post {
    width: 640px;
  }

}

@media (min-width: 992px) and (max-width: 1200px) {

  .feed {
    width: 100%;
  }

  .new_post {
    width: 840px;
  }

}

@media (min-width: 1200px) and (max-width: 1400px) {

  .feed {
    width: 700px
  }

  .new_post {
    width: 640px;
  }

}

@media (min-width: 1400px) {
  .feed {
    width: 840px;
  }
}