
.post {
    width: 800px;
    padding: 30px;
    border-bottom: 1px solid #000;
    background-color: #000;
    margin-top: 25px;
    border-radius: 50px;
    color: #fff;
}

.text {
    padding-left: 60px;
    font-size: 15px;
    padding-bottom: 10px;
    color: #fff;
    font-weight: 300;
}

.top {
    display: flex;
    width: 100%;
    color: #fff;
}
.top>div:first-child {
    margin-right: 10px;
}

.name {
    display: flex;
    color: #fff;
}

@media screen and (max-width: 576px) {

    .post_container {
        width: 100%;
    }
    .post {
        width: 100%;
        padding: 25px;
        border-radius: 30px;
    }
    .text {
        padding: 5px 0 0 0;
        font-size: 14px;
    }

}
  
@media (min-width: 576px) and (max-width: 768px) {

    .post_container {
        width: 100%;
    }
    .post {
        width: 100%;
    }

}

@media (min-width: 768px) and (max-width: 992px) {

    .post {
        width: 640px;
    }

}

@media (min-width: 992px) and (max-width: 1200px) {
    
    .post {
        width: 840px;
    }

}

@media (min-width: 1200px) and (max-width: 1400px) {

    .post {
        width: 640px;
    }

}

