.error {
    color: red;
}

.form>div>input {
    background-color: #000;
    padding: 10px 10px;
    width: 600px;
    outline: none;
    text-align: center;
    font-size: 15px;
    border: none;
    border-bottom: 1px solid #fff;
}

.form>div>input:active {
    background-color: #000;
    outline: none;
}

.form>div {
    display: flex;
    justify-content: center;
    margin-bottom: 30px;
}

.form>div:last-child {
    margin-bottom: 0;
}

.checkbox>input {
    width: 50px !important;
}

.form_container {
    width: 100%;
    display: flex;
    justify-content: center;
}

.container {
    width: 100%;
    padding-bottom: 50px;
}

.span {
    color: white;
    text-align: center;
    font-family: 'Bayon', sans-serif;
    font-weight: 400;
}

.form {
    background-color: #000;
    height: 700px;
    width: min-content;
    border-radius: 50px;
    padding: 25px 100px;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.btn {
    border-radius: 100px;
    border: none;
    padding: 10px 20px;
    background-color: #f7d932;
    color: black;
    cursor: pointer;
    transition: 0.3s all ease;
}

.captcha {
    display: flex;
}

.captcha>div {
    width: 300px;
    display: flex;
    justify-content: center;
    align-self: center;
    max-height: 50px;
}

.captcha>input {
    width: 300px !important;
}

.btn:hover {
    background-color: #a89421;
    transition: 0.3s all ease;
}

.btn:active {
    background-color: #917f1c;
}

.form>div>span {
    font-weight: 300;
}

@media (max-width: 576px) {
    .form {
        height: 300px;
        padding: 15px 30px;
        border-radius: 25px;
    }
    .form>div>input {
        padding: 10px 10px;
        width: 200px;
        font-size: 14px;
    }
    .form>div>span {
        font-size: 14px;
        font-weight: 300;
    }
}

@media (min-width: 576px) and (max-width: 768px) {
    .form {
        height: 500px;
        padding: 25px 50px;
    }
    .form>div>input {
        padding: 10px 10px;
        width: 400px;
    }
}

@media (min-width: 768px) and (max-width: 992px) {
    .form {
        height: 600px;
        padding: 25px 50px;
    }
    .form>div>input {
        padding: 10px 10px;
        width: 500px;
    }
}

