.header {
  height: 100px;
  width: 100%;
  text-align: center;
  font-size: 30px;
  background: #000;
  display: flex;
  z-index: 1000;
  position: absolute;
}

.account {
  display: flex;
  align-items: center;
}

@media screen and (max-width: 576px) {
  .center {
    width: min-content;
    margin: 0 auto;
  }
  .left {
    width: 0;
  }
  .header {
    height: 50px;
  }
  .left {
    width: 20%;
  }
  .menu_btn_container {
    width: 20%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
  }
  .menu {
    width: 100%;
    background-color: black;
    z-index: 500;
    position: absolute;
    top: 50px;
    left: 0;
    transition: all 0.5s ease;
    padding: 20px;
  }
}

@media (min-width: 576px) and (max-width: 768px) {
  .header {
    height: 100px;
  }
  .center {
    width: min-content;
    margin: 0 auto;
  }
  .left {
    width: 20%;
  }
  .menu_btn_container {
    width: 20%;
    color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 35px;
  }
  .menu {
    width: 100%;
    background-color: black;
    z-index: 500;
    position: absolute;
    top: 100px;
    left: 0;
    transition: all 0.5s;
    padding: 20px;
  }
}

@media (min-width: 768px) and (max-width: 992px) {
  .left {
    width: 10%;
  }
  .center {
    width: 70%;
  }
  .account {
    width: 20%;
  }
}

@media (min-width: 992px) and (max-width: 1200px) {
  .left {
    width: 10%;
  }
