.container {
    width: 100%;
    padding: 25px;
    color:white;
}

.bg {
    width: 100%;
    background-color: #000;
    border-radius: 50px;
    padding: 50px;
    min-height: 800px;
}

.fullName {
    max-width: 80%;
    font-size: 30px;
    overflow: hidden;
    text-overflow: ellipsis;
}

.aboutMe {
    font-size: 20px;
}

.img_container {
    width: 15%;
    height: min-content;
}

.texts {
    width: 25%;
    height: min-content;
    padding: 25px 0 0;
}

.status {
    padding: 15px 25px;
}

.edit_button {
    color: #000;
    padding: 5px 15px;
    border-radius: 100px;
    border: none;
    vertical-align: text-bottom;
    margin-left: 20px;
    cursor: pointer;
    transition: all 0.3s;
}

.edit_button:hover {
    background-color: rgb(212, 212, 212);
    transition: all 0.3s;
}

.edit_span_div {
    text-align: center;
    margin-bottom: 30px;
}

.edit_span {
    font-family: 'Bayon', sans-serif;
    font-size: 35px;
}

.x {
    color: white;
    position: relative;
    left: 450px;
    top: -25px;
    cursor: pointer;
    transition: 0.4s all ease;
}

.x:hover {
    color: #f7d932;
    transition: 0.4s all ease;
}

.contacts_container {
    width: 60%;
}

.contacts {
    float: right;
    height: min-content;
}

.contact_container {
    padding: 3px 5px;
    display: flex;
    flex-wrap: nowrap;
    max-width: 200px;
    overflow: hidden;
    transition: all 0.4s ease;
}

.contact_logo {
    font-size: 20px;
    margin-right: 10px;
    color:inherit;
}

.contact_text {
    overflow: hidden;
    text-overflow: ellipsis;
}

.instagram:hover {
    color: #e9399d;
    transition: all 0.4s ease;
}

.twitter:hover {
    color: #1da1f2;
    transition: all 0.4s ease;
}

.facebook:hover {
    color: #1877f2;
    transition: all 0.4s ease;
}

.github:hover {
    color: #6cc644;
    transition: all 0.4s ease;
}

.youtube:hover {
    color: #ff0000;
    transition: all 0.4s ease;
}

.site:hover {
    color: rgb(0, 140, 255);
    transition: all 0.4s ease;
}

.texts>div {
    overflow: hidden;
    text-overflow: ellipsis;
    display: flex;
}

@media screen and (max-width: 359.98px) {
    .img_container {
        width: 100px;
        height: min-content;
    }
    .texts {
        width: 80%;
        min-width: 150px;
        height: min-content;
        padding: 10px 0 0 0;
    }
    .fullName {
        font-size: 18px;
    }
    .aboutMe {
        font-size: 15px;
    }
    .edit_button {
        font-size: 12px;
    }
    .contacts_container {
        width: min-content;
        height: min-content;
        padding-top: 20px;
    }
    .contact_container {
        padding-left: 0;
    }
    .bg {
        border-radius: 40px;
        padding: 30px;
    }
    .bg>div:first-child {
        flex-wrap: wrap;
        justify-content: center;
    }
    .contacts>* {
        font-size: 14px;
    }
    .status {
        font-size: 14px;
        padding-left: 0;
        padding-right: 0;
    }
}

