.login {
    color: white;
    font-size: 20px;
    display: block;
    text-align: left;
    padding-left: 5px;
    white-space: nowrap;
    overflow: hidden;
    max-width: 100%;
    text-overflow: ellipsis;
    padding-right: 10px;
}

.email {
    color: white;
    font-size: 15px;
    display: block;
    text-align: left;
    padding-left: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
}

.login_container {
    width: 200px;
    display: flex;
}

.text {
    width: 100%;
    overflow: hidden;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
}

.text>a {
    overflow: hidden;
    max-width: 100%;
    text-overflow: ellipsis;
    padding-right: 10px;
}

.login_buttons {
    color: white;
    display: flex;
    font-size: 20px;
    justify-content: space-between;
    align-items: baseline;
    padding-bottom: 10px;
}

.text_buttons {
    color: white;
}

.text_buttons:hover {
    color: #f7d932;
}

.logout {
    color: white;
    font-size: 14px;
    display: block;
    text-align: left;
    padding-left: 5px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    cursor: pointer;
}

.logout:hover {
    color: red;
}

@media screen and (max-width:576px) {
    .login_container {
        margin: 0 auto;
        max-width: 250px;
        display: flex;
        justify-content: center;
    }
    .login {
        font-size: 15px;
        padding-right: 0;
    }
    .login_buttons {
        justify-content: center;
    }
    .text {
        padding: 0 0 0 10px;
    }
}

@media (min-width:576px) and (max-width:768px) {
    .login_container {
        margin: 0 auto;
        width: 160px;
        display: flex;
        justify-content: center;

    }

    .login_buttons {
        justify-content: center;
    }

    .login {
        overflow: hidden;
    }
}

@media (min-width:768px) and (max-width:992px) {
    .login_container {
        margin: 0 auto;
        width: 150px;
        display: flex;
        justify-content: center;
    }
}