.buttons {
  display: flex;
  justify-content: space-between;
  padding: 10px 30px 20px;
  height: 25px;
}

.btn {
  cursor: pointer;
}

.like {
  font-size: 20px;
  transition: all 0.4s;
}

.comment {
  font-size: 20px;
  transition: all 0.4s;
}

.share {
  font-size: 20px;
  transition: all 0.4s;
}

.hide {
  font-size: 20px;
  transition: all 0.4s;
}

.like:hover {
  color: #f7d932;
  transition: all 0.4s;
}

.comment:hover {
  color: rgb(129, 129, 129);
  transition: all 0.4s;
}

.share:hover {
  color: rgb(123, 169, 255);
  transition: all 0.4s;
}

.hide:hover {
  color: gray;
  transition: all 0.4s;
}

@media screen and (max-width:576px) {
  .like {
    font-size: 18px;
    transition: all 0.4s;
  }
  
  .comment {
    font-size: 18px;
    transition: all 0.4s;
  }
  
  .share {
    font-size: 18px;
    transition: all 0.4s;
  }
  
  .hide {
    font-size: 18px;
  }
  .buttons {
    width: 100%;
    padding: 10px 15px 20px;
  }
}
  
@media (min-width: 576px) and (max-width: 768px) {
  .buttons {
    width: 50%;
  }
}

@media (min-width: 768px) and (max-width: 992px) {
  .buttons {
    width: 45%;
  }
}

@media (min-width: 992px) and (max-width: 1200px) {
  .buttons {
    width: 30%;
  }
}

@media (min-width: 1200px) and (max-width: 1400px) {
  .buttons {
    width: 40%;
  }
}

@media (min-width: 1400px) {
  .buttons {
    width: 30%;
  }
}