.brand > a {
    margin: 0 auto;
    padding-top: 10px;
    cursor: pointer;
    font-size: 60px;
    font-family: 'Lovers Quarrel', cursive;
    color: #f7d932;
}

.brand {
    width: 20%;
    padding-top: 15px;
}

@media screen and (max-width: 576px) {
    .brand {
        padding-top: 0;
    }
    .brand > a {
        padding-top: 0;
        font-size: 30px;
    }
}

@media (min-width: 576px) and (max-width: 768px) {
    .brand {
        padding-top: 10px;
    }
    .brand > a {
        padding-top: 0;
        font-size: 60px;
    }
}